.login-component {
  position: relative;
  height: 100vh;
  background-color: rgba(235, 217, 135, 0.348);

}

.auth-card {
  font-weight: 600;
  font-size: 18px;
  width: 350px;
  border: 3px dotted white;
  padding: 25px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.2s;
  animation: zoom 0.5s ease-out;

  &:hover {
    box-shadow: 10px 5px 5px skyblue;
  }

  & input {
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-size: small;
    font-weight: 300;
    display: block;
    margin-bottom: 12px;
    width: 100%;
    padding: 5px;
    border: 2px dotted rgb(185, 180, 180);
    text-transform: none;
  }

  & .text-center button {
    padding: 3px 10px;
    margin: 20px 0;
    background-color: seagreen;
    border: none;
    cursor: pointer;
    transition: all 0.2s;
  }

  & .text-center button:hover {
    background-color: rgb(30, 206, 106);
    box-shadow: 10px 5px seagreen;
  }

  & .text-center button:active {
    box-shadow: 5px 2.5px seagreen;
  }

  & a {
    transition: all 0.2s;
  }

  & a:hover {
    background-color: skyblue;
  }

  & .header {
    padding: 25px 0;
    font-size: 25px;
    color: white;
    background-color: rgb(143, 131, 226);
    margin-bottom: 20px;
  }

  & .error-msg {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    font-size: 16px;
    letter-spacing: 1px;
    background-color: rgb(244, 123, 123);
    margin-bottom: 10px;
  }
}

@keyframes zoom {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
