.Card {
  background-color: whitesmoke;
  border-radius: 10px;
  padding: 10px;
  max-width: 900px;

  & .quote {
    padding: 10px;
    font-size: 20px;
    font-weight: bolder;
  }

  & blockquote {
    display: inline;
  }

  .read-more {
    border-bottom: 2px dotted #f78f65;
    margin-left: 5px;
  }

  & img {
    border-radius: 100%;
    padding-bottom: 5px;
    height: 100px;
  }

  & .img-section {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
    border-bottom: 2px dotted #f78f65;
    margin-bottom: 20px;
  }

  & .card-title {
    font-size: 30px;
  }

  & a {
    all: unset;
  }

  & a:hover {
    background-color: #f78f65;
    transition: all;
    transition-duration: 0.8s;
    cursor: pointer;
  }
}
