.Testimonies {
  background-color: #e8d9d3;
  height: 100vh;
  position: relative;
}

.title {
  padding-top: 20px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.carousel {
  margin-top: 50px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.btn-rounded {
  background-color: none;
  color: #dbb4a3;
  font-size: 40px;
  position: absolute;
  top: 50%;
  cursor: pointer;
}

.btn-rounded:hover {
  color: #f78e65bb;
}

.btn-left {
  left: 20px;
}

.btn-right {
  right: 20px;
}


