.TrackList {
  height: 100vh;
  background-color: #c9d7e2;
}

.primary-heading {
  margin-bottom: 40px;
}

.main-content {
  padding-top: 40px;
  display: flex;
  row-gap: 20px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.link {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  color: black;
  transition: color .2s;
}

.link:hover {
  color: salmon;
}

