.Intro {
  height: 100vh;
  background-image: url("https://images.pexels.com/photos/1533720/pexels-photo-1533720.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1");
  background-size: cover;
  background-position: top;
  font-weight: 900;
}

.content {
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translate(-50%, -25%);
  text-align: center;
}

.main-title {
  font-size: 44px;
  animation-name: moveFromLeft;
  animation-duration: 1s;
  animation-timing-function: ease-out;
  border-bottom: 2px dotted black;
  display: inline-block;
  margin-bottom: 10px;
}

.sub-title {
  font-size: 36px;
  animation: moveFromRight 1s ease-out;
}

.btn-white {
  padding: 10px 15px;
  background-color: white;
  color: black;
  margin-top: 80px;
  animation: moveFromBottom 1s ease-out 1s;
  transition: all 0.2s;
}

.btn-white:hover {
  transform: translateY(-4px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.btn-white:active {
  transform: translateY(-2px);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}

@keyframes moveFromBottom {
  0% {
    opacity: 0;
    transform: translateY(100px);
  }

  75% {
    opacity: 0.75;
    transform: translateY(-25px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes moveFromLeft {
  0% {
    opacity: 0;
    transform: translateX(-100px);
  }

  75% {
    opacity: 0.75;
    transform: translateX(25px);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes moveFromRight {
  0% {
    opacity: 0;
    transform: translateX(100px);
  }

  75% {
    opacity: 0.75;
    transform: translateX(-25px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
