@media (max-width: 300px) {
  .box-3 {
    width: 150px;
  }

  .TrackItem {
    gap: 5px;
  }
}

@media (min-width: 301px) {
  .box-3 {
    width: 200px;
  }
  .TrackItem {
    gap: 8px;
  }
}

@media (min-width: 400px) {
  .box-3 {
    width: 300px;
  }
  
  .TrackItem {
    gap: 10px;
  }
}

@media (min-width: 550px) {
  .box-3 {
    width: 400px;
  }

  @media (min-width: 700px) {
    .box-3 {
      width: 500px;
    }
  }
}
